.stars {
  display: flex;
  margin: 0;
  padding: 0;
}

.stars__star {
  height: 16px;
  width: 16px;
  margin-right: 4px;
  background-image: url(images/star-active.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.stars--4 .stars__star:nth-of-type(n+5) {
  background-image: url(images/star.svg);
}
